<template>
    <div class="sheet" :style="{background:color}">
        <div @click="deleteSheet" class="delete-sheet">
            <span></span>
        </div>
        <div class="sheet-heading">{{heading}}</div>
        <div class="sheet-text">{{text}}</div>    
        <time class="sheet-date">Створено {{day}} / {{month}} / {{year}}</time>   

        <div class="edit" @click="editOpened">
            <img src="../assets/Icons/edit.svg" alt="">
        </div>

       
    </div>
</template>

<script>

export default {
    name:'Sheet',

    data(){
        return{
          showColors:false
        }
    },

    props:{
        'id':Number,
        'heading':String,
        'text' : String,
        'category':String,
        'date':Number,
        'day': Number,
        'month':String,
        'year':Number,
        'color':{
            type:String,
            default:'rgba(27,27,27,0.8)'
        }
    },

    methods:{
        showAll(){
            
        },

        changeColor(){
           
        },
        
        deleteSheet(e){
            e.stopPropagation();
            this.$emit('delete-sheet',this.id);
        },

        editOpened(){
            this.$emit('edit-open',this.id,this.color);
        }

    },

    mounted(){
        this.$emit('sheet-mounted',this.$el);
    }

}
</script>

<style scoped>
    .sheet{
        margin: 10px 30px;
        padding: 20px;
        height: 100%;
        color: #fff;
        position: relative;
        transition: .3s;
        width: calc((100% / 3) - 60px);
        min-width: 270px;
        box-sizing: border-box;
        box-shadow: 0 3px 12px 0 rgba(0,0,0,.5);
    }

    @media screen and (max-width: 1250px){
        .sheet{
            width: calc((100% / 2) - 60px);
        }
    }

    @media screen and (max-width: 830px){
        .sheet{
            margin:20px auto;
            width: 90%;
        }
    }

    .sheet:hover{
        transform: scale(1.05);
    }

    .delete-sheet{
        width: 25px;
        height: 25px;
        position: absolute;
        right: 5px;
        top:5px;
        cursor: pointer;
        opacity: 0.8;
        transition: .1s;
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        user-select: none;
    }

    .delete-sheet span{
        display: block;
        width: 100%;
        height: 3px;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
    }

    .delete-sheet span:before{
        content: '';
        width: inherit;
        height: inherit;
        background: #AFAAAA;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%) rotate(45deg);
        transition: .3s;
    }

    .delete-sheet span:after{
        content: '';
        width: inherit;
        height: inherit;
        background: #AFAAAA;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%) rotate(-45deg);
        transition: .3s;
    }

    .delete-sheet:hover{
        opacity: 1;
        color: #AF182F;
    }

    .delete-sheet:hover span:before,.delete-sheet:hover span::after{
        background: rgba(175,65,73);
    }

    .edit{
        position: absolute;
        width: 25px;
        height: 25px;
        bottom: 5px;
        right: 5px;
        cursor: pointer;
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        user-select: none;
    }

    .edit img{
        width: 100%;
        height: 100%;
    }

    .sheet-text{
        padding: 15px 0 15px 0;
        text-align: center;
        
        font-size: 22px;
        word-wrap: break-word;
    }

    .sheet-date{
        font-size: 14px;
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        user-select: none;
        position: relative;
        top: 10px;
    }

</style>
