                <div class="expanded-button-open"><span @click="expandedShow = !expandedShow">Розширене додавання</span></div>
                <div class="expanded-controll" v-show="expandedShow">
                <input v-model="sheetHeading" type="text" placeholder="Заголовок">
                <input v-model="sheetCategory" type="text" placeholder="Категорія">

