<script>

export default {

  name: 'ControllPanel',

  data(){
    return{
      bounce : false,
      fastAddingShow : false ,
      settingsShow:false,
      expandedShow:false,
      categories:[],
      sheetText:'',
      sheetHeading:'',
      sheetCategory:'',
      searchQuery:'',
      sortByAge:false
    }
  },

  methods:{
    openCategories(){
      this.$emit('open-categories');
    },

    addSheet(){
      if(this.sheetText.length >= 1){
        this.$emit('add-sheet',this.sheetText,this.sheetHeading,this.sheetCategory); 
        this.$emit('category-added');
        this.sheetText = '';
        this.sheetHeading = '';
        this.sheetCategory = '';
        this.fastAddingShow = !this.fastAddingShow;
      }
    },

    sortStart(){
      this.$emit('sort',this.sortByAge);
      this.sortByAge = !this.sortByAge;
    }
  }

}
</script>

