<template>
    <div>
        <div class="overlay"></div>
        <div class="edit-container" :style="{background:color}">
            <div class="close-edit" @click="$emit('edit-close')">
                <span></span>
            </div>
            <input :value="heading" type="text" placeholder="Заголовок" class="input input-heading">
            <textarea :value="text" placeholder="Текст" class="input text"></textarea>
            <input :value="category" type="text" placeholder="Категорія" class="input input-category">
            
            <div class="color-picker">
                <div  class="colors-container">
                    <div @click="changeColor('rgba(27,27,27,0.8)')" class="color default-dark"></div>
                    <div @click="changeColor('rgba(109,175,82,0.9)')" class="color green"></div>
                    <div @click="changeColor('rgba(112,75,175,0.9)')" class="color violet"></div>
                    <div @click="changeColor('rgba(75,75,255,0.9)')" class="color blue"></div>
                </div>
            </div>

            <div class="buttons-container">
                <button @click="save" class="button save">Зберегти</button>
                <button @click="cancel" class="button cancel">Скасувати</button>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    name:'Edit',
    
    data(){
        return{
            pickedColor:'',
            selected:''
        }
    },

    props:{
        'heading':String,
        'category':String,
        'text':String,
        'picked':{
            type:Number,
            default:0
        },
        'color':{
            type:String,
            default:'rgba(27,27,27,0.8)'
        }
    },

    methods:{
        save(){
            let inpHeading = document.querySelector('.input-heading').value;
            let inpCategory = document.querySelector('.input-category').value;
            let inpText = document.querySelector('.text').value;
            let color = this.color;
            
            if(this.pickedColor != ''){
                let color = this.pickedColor;
            }

            this.$emit('save',inpHeading,inpCategory,inpText,color,this.selected);
            this.$emit('category-added');
        },

        cancel(){
            this.$emit('edit-close');
        },

        changeColor(col){
            this.pickedColor = col;
            let inpHeading = document.querySelector('.input-heading').value;
            let inpCategory = document.querySelector('.input-category').value;
            let inpText = document.querySelector('.text').value;
            this.pickColor();
            this.$emit('color-changed',col,inpHeading,inpCategory,inpText);
        },

        pickColor(){
            let colors = document.querySelectorAll('.color');

            colors.forEach((element,index) => {
                element.classList.remove('active');
            }) 
            
            event.target.classList.add('active');

            colors.forEach((el,index) => {
                if(el.classList.contains('active')) this.selected = index;
            })
            
            
            console.log(this.selected);
        }
    },

    mounted(){
        let colors = document.querySelectorAll('.color');
        colors[this.picked].classList.add('active');
    }
}

</script>
<style scope>
    .edit-container{
        width: 50%;
       
        padding: 30px 40px;
        position: fixed;
        left: 50%;
        top:50%;
        transform: translate(-50%,-50%);
        transition: .3s;
    }

    .input{
        padding: 20px 30px;
        margin: 30px auto;
        width: 60%;
        display: block;
        background: transparent;
        border: none;
        outline: none;
        border-bottom: 1px solid #A8A3A3;
        color: #FFFFFF;
        resize: none;
        font-size: 18px;
         word-wrap: break-word;
    }

    .text{
        padding: 30px 30px;
        height: 100%;
        white-space: pre;
    }

    .close-edit{
        width: 20px;
        height: 20px;
        position: absolute;
        right: 30px;
        top: 30px;
        cursor: pointer;
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        user-select: none;
    }

    .close-edit span{
        display: block;
        width: 100%;
        height: 3px;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
    }

    .close-edit span:before{
        content: '';
        width: inherit;
        height: inherit;
        background: #AF3E4B;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%) rotate(45deg);
    }

    .close-edit span:after{
        content: '';
        width: inherit;
        height: inherit;
        background: #AF3E4B;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%) rotate(-45deg);
    }

    .buttons-container{
        margin: 20px auto 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .button{
        margin: 0 30px;
        display: block;
        border: none;
        cursor: pointer;
        height: 30px;
        width: 100px;
        color: #fff;
        text-transform: uppercase;
        outline: none;
        font-weight: bold;
    }

    .save{
        background: #2A622D;
    }

    .cancel{
        background: #AF3E4B;
    }

    /* COLOR PICKER */

    .color-picker{
        position: absolute;
        right: 30px;
        top:50%;
        transform: translateY(-50%);

    }

    .color{
        margin: 5px 0;
        width: 30px;
        height: 30px;
        background: red;
        cursor: pointer;
        transition: .3s;
        position: relative;
    }
    .active{
        transform: scale(1.1);
    }
    .active:before{
        content:url('../assets/Icons/check.svg');
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
    }

    .color:hover{
        cursor: pointer;
        transform: scale(1.1);
    }

    .default-dark{
        background: #1B1B1B;
    }

    .blue{
        background: blue;
    }

    .green{
        background: green;
    }

    .violet{
        background: rgb(112,75,175);
    }
</style>