<template>
    <div class="categories-container">
        <ul class="categories-list">
            <div class="your-categories no-border-top">Стандартні</div>
            <span @click="pickActiveCategory">
                <li @click="filter('')"  class="category category-active">Всі</li>
            </span>
            <span @click="pickActiveCategory">
                <li @click="filter('important')"  class="category">Важливі</li>
            </span>
            <div class="your-categories">Ваші категорії</div>
            <span @click="pickActiveCategory">
                <li @click="filter(item)" class="category" v-for="(item,index) in todoCategories" v-bind:key="index">{{item}}</li>
            </span>
        </ul>
    </div>
</template>

<script>

export default {
    name:'categories',

    data(){
        return{
            todoCategories : this.$props.categories,
            activeCategory:'Всі'
        }
    },

    props:{
        'categories': Array
    },

    methods:{
        filter(item){
            this.$emit('filter',item);
        },

        pickActiveCategory(e){
            if(e.target.classList.contains('category')){

                //clear all cateogories
                let allCategories = document.querySelectorAll('.category');
                allCategories.forEach((el) => {
                    el.classList.remove('category-active')
                })

                //pick active category 
                e.target.classList.add('category-active');
                this.activeCategory = e.target.innerText;
                this.$emit('category-changed',this.activeCategory);
            }
        },

        updateCategories(array){
            this.todoCategories = [];

            array.forEach((el) => {

                this.todoCategories.forEach((elem,index) => {
                    if(elem == el){
                       this.todoCategories.splice(index,1);
                    }   
                })

                this.todoCategories.push(el);
            })   
        }
    }

}
</script>

<style>

    .categories-container{
        width: 270px;
        min-height: 100%;
        background: #1B1B1B;
        position: fixed;
        top:0;
        left: 0;
        bottom: 0;
        z-index: 3;
    }

    .categories-heading{
        font-size: 35px;
        text-align: center;
        color: red;
    }

    .categories-list{
        margin: 90px 0 0;
        padding: 0;
    }

    .your-categories{
        border-top: 3px solid #A9A4A4;
        padding-top: 20px;
        margin: 30px 0;
        text-align: center;
        font-size: 30px;
        color: #A9A4A4;
    }

    .no-border-top{
        border-top: none;
    }

    .category{
        list-style: none;
        font-size: 23px;
        color: #A9A4A4;
        text-align: center;
        padding: 5px;
        margin: 15px 0;
        cursor: pointer;
    }

    .category-active{
        color: rgb(114, 66, 66);
    }

    .category:hover{
        color: rgb(114, 66, 66);
    }
    

    .categories-enter{
        transition: .1s;
        transform: translateX(-50%);
    } 

    .categories-enter-active{
        transition: .3s;
        left:0;
    }

    .categories-leave-to {
       transition: .3s;
       transform: translateX(-100%);
    }
    
    

</style>