<template>
  <div id="app">
    <controll-panel  @add-sheet="addSheet" @open-categories="openCategories" @sort="sortSheets"/>

      <div class="container" >
            
            <sheet 
                v-for="(item ,index) in todos" 
                v-bind:id="index"
                v-bind:heading="item.heading"
                v-bind:text="item.text" 
                v-bind:color="item.color"
                v-bind:date="item.date"
                v-bind:day="item.day"
                v-bind:month="item.month.toString()"
                v-bind:category="item.category"
                v-bind:year="item.year"
                v-bind:key="index"
                v-on:delete-sheet="deleteSheet"
                @sheet-mounted="saveLink"
                @edit-open="editOpen"
                v-bind:draggable="draggable"
              />
            
          </div>

      <transition name="edit">
        <Edit
         v-if="editingShow" 
         @color-changed="changeEditBG"
         @edit-close="editClose"
         @save="saveSheet" 
         
         :heading="selectedSheetHeading" 
         :category="selectedSheetCategory"
         :text="selectedSheetText"
         :color="selectedSheetColor"
         :picked="selectedSheetPickedColor"
        />
      </transition>

    <transition name="categories">
      <categories ref="categories"  v-show="categoriesOpened" v-bind:categories="categories" @filter="filterCategories" @category-changed="changeCategory"/>
    </transition>

    <transition name="overlay" >
      <div @click="categoriesOpened = !categoriesOpened" v-show="categoriesOpened" class="overlay"></div>
    </transition>

    <div class="active-category-message" v-show="activeCategoryText.length">
      <div class="active-category-message-close" @click="closeMessage"><span></span></div>
      Показано <span class="message">" {{activeCategoryText}} "</span>
    </div>
     
  </div>
</template>

<script>

import ControllPanel from './components/ControllPanel.vue'
import Sheet from './components/Sheet.vue'
import Categories from './components/Сategories.vue'
import Edit from './components/Edit.vue'


export default {
  name: 'app',

  components: {
    ControllPanel,
    Sheet,
    Categories,
    Edit
  },

  data(){
     return{
       todos : [],
       todosLinks : [],
       categories: [],
       categoriesOpened:false,
       draggable:false,
       activeCategoryText:'',
       sortByDateNewFirst:false,
       editingShow:false,
       selectedSheet:'',
       selectedSheetHeading:'',
       selectedSheetCategory:'',
       selectedSheetText:'',
       selectedSheetColor:'',
       selectedSheetPickedColor:0
     }
  },

  methods:{
    deleteSheet(id){
      this.todos.splice(id,1);
    },

    addSheet(text,heading,category){
      let date = new Date();
      let newItem = {
        text : text,
        heading:heading,
        category:category,
        important:false,
        date:date.getTime(),
        day: date.getDate(),
        month:date.getMonth() == 0 ? 1 : date.getMonth() + 1,
        year:date.getFullYear(),
        color:'rgba(27,27,27,0.8)',
        picked:0
      }

      if(newItem.category.length < 1){
        newItem.category = 'Без категорії';
      }

      if(newItem.heading.length < 1){
        newItem.heading = newItem.text.split(' ')[0];
      }

      if(newItem.month <= 9){
        newItem.month = '0' + newItem.month;
      }

      this.todos.push(newItem);
    },

    openCategories(){
      this.categoriesOpened = !this.categoriesOpened;
    },

    filterCategories(item){

      if(item === ''){
        this.todosLinks.forEach(el => {
          el.style.display = 'block';
        })
        this.categoriesOpened = !this.categoriesOpened;
        return;
      }

      if(item === 'important'){
          this.todosLinks.forEach((el,i) => {
            if(this.todos[i].important){
              el.style.display = 'block';
            }else{
              el.style.display = 'none';
            }
            
          })
          this.categoriesOpened = !this.categoriesOpened;
          return;
      }  

      this.todos.forEach((el,index) => {
        if(el.category === item){
          this.todosLinks[index].style.display = 'block';
        }else{
          this.todosLinks[index].style.display = 'none';
        }
      })

      this.categoriesOpened = !this.categoriesOpened;
        
    },

    saveLink(el){
      this.todosLinks.push(el);
    },

    changeCategory(categorySelected){
      let message = document.querySelector('.active-category-message');
      message.style.display = 'none';
      message.style.display = 'block';
      this.activeCategoryText = categorySelected;
    },
    closeMessage(){
      document.querySelector('.active-category-message').style.display = 'none';
    },

    sortSheets(sortByAge){
      if(this.todos.length < 1){
        return;
      }

      let compareByDate;

      if(sortByAge){
        compareByDate = function (a,b) {

          //sorting new first
          
          if (a.date > b.date)
            return -1;
          if (a.date < b.date)
            return 1;
          return 0;

          
        }

      }else{
        //sorting old first

        compareByDate = function (a,b) {
          if (a.date < b.date)
            return -1;
          if (a.date > b.date)
            return 1;
          return 0;
        }

      }

      this.todos = this.todos.sort(compareByDate);
    },

    changeEditBG(col,heading,category,text){
      this.selectedSheetColor = col;
      this.selectedSheetHeading = heading;
      this.selectedSheetCategory = category;
      this.selectedSheetText = text;
    },

    editOpen(id,color){
      this.editingShow = true;
      this.selectedSheet = id;
      this.selectedSheetHeading = this.todos[this.selectedSheet].heading;
      this.selectedSheetCategory = this.todos[this.selectedSheet].category;
      this.selectedSheetText = this.todos[this.selectedSheet].text;
      this.selectedSheetColor = this.todos[this.selectedSheet].color;
      this.selectedSheetPickedColor = this.todos[this.selectedSheet].picked;
    },

    editClose(){
      this.selectedSheetHeading = this.todos[this.selectedSheet].heading;
      this.selectedSheetCategory = this.todos[this.selectedSheet].category;
      this.selectedSheetText = this.todos[this.selectedSheet].text;
      this.editingShow = false;
    },

    saveSheet(heading,category,text,color,selected){
      if(heading === '' && text === ''){
        this.deleteSheet(this.selectedSheet);
        this.editingShow = false;
        return;
      }

      this.todos[this.selectedSheet].heading = heading;
      this.todos[this.selectedSheet].category = category;
      this.todos[this.selectedSheet].text = text;
      this.todos[this.selectedSheet].color = color;
      this.todos[this.selectedSheet].picked = selected;

      this.editingShow = false;

      let jsonTodos = JSON.stringify(this.todos);
      window.localStorage.setItem('todos',jsonTodos);

      this.categories = [];
      this.todos.forEach((el)=>{
        if(el.category.length > 0){
          this.categories.push(el.category);
        }
      })

      this.$refs.categories.updateCategories(this.categories);
    }
  },

  watch:{
    todos(){
      let jsonTodos = JSON.stringify(this.todos);
      window.localStorage.setItem('todos',jsonTodos);

      //categories refresh

      this.categories = [];
      this.todos.forEach((el)=>{
        if(el.category.length > 0){
          this.categories.push(el.category);
        }
      })

      this.$refs.categories.updateCategories(this.categories);
      
    }
  },

  created(){
    if(window.localStorage.getItem('todos') != null){
      JSON.parse(window.localStorage.getItem('todos')).forEach((el) => {
        this.todos.push(el);
      })
    }

    this.categories = [];
      this.todos.forEach((el)=>{
      if(el.category.length > 0){
        this.categories.push(el.category);
      }
    })

  }
}
</script>

<style>

body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #2C2C2C;
}

.overlay{
  position: fixed;
  width: 100%;
  height: 100%;
  left:0;
  top: 0;
  bottom: 0;
  background: #000;
  opacity: 0.7;
}

.container{
  margin: 115px auto 30px;
  width: 90%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}


[draggable=true]{
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  /* Required to make elements draggable in old WebKit */
  -khtml-user-drag: element;
  -webkit-user-drag: element;
  cursor: move;
  opacity: 1;
  
}

.active-category-message{
  position: fixed;
  padding: 10px 25px;
  height: 50px;
  line-height: 50px;
  background: #1B1B1B;
  text-align: center;
  font-size: 25px;
  bottom: 10px;
  right: 30px;
  color: #A9A4A4;
  -webkit-box-shadow: 2px 9px 50px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 2px 9px 50px 0px rgba(0,0,0,0.75);
  box-shadow: 2px 9px 50px 0px rgba(0,0,0,0.75);
  
}

.active-category-message .message{
  color: #C8C8C8;
}

.active-category-message-close{
  width: 20px;
  height: 20px;
  position: absolute;
  top:2px;
  right: 2px;
  cursor: pointer;
}

.active-category-message-close span{
  position: absolute;
  width: 100%;
  height: 3px;
  left:0;
  top:50%;
  transform: translateY(-50);
  
}

.active-category-message-close span:before{
  content:'';
  position: absolute;
  width: 100%;
  height: 3px;
  top:0;
  left:0;
  background: #AF3E4B;
  transform: rotate(45deg);
}

.active-category-message-close span:after{
  content:'';
  position: absolute;
  width: 100%;
  height: 3px;
  top:0;
  left:0;
  background: #AF3E4B;
  transform: rotate(-45deg);
}

/* @TODO ANIMATION FOR ENTER */

.edit-enter-active{
  transition: .3s;
}

.edit-leave-active{
  transition: .5s;
  transform: translate(-50%,-50%) scale(0);
}

</style>
